





















































































































































import { Component, Vue } from 'vue-property-decorator';
import { Member } from '@/views/manage/member/common/types';
import {
  createDataApi,
  deleteDataApi,
  getCodeListDataApi,
  getOnlyListDataApi,
  patchDataApi,
} from '@/utils/api';
import { DataTableHeader, SelectItem } from '@/common/types';
import DatetimePicker from '@/components/picker/DatetimePicker.vue';
import { Validation } from 'vuelidate';
import {
  delayTouch,
  getVErrors,
  maxLength,
  required,
} from '@/utils/validation-helper';
import _ from 'lodash';

@Component({
  components: { DatetimePicker },
  validations: {
    member: {
      memberId: {
        required,
        maxLength: maxLength(20),
      },
      memberPw: {
        required(value: string) {
          if (
            !this.$data.isUpdate ||
            (this.$data.memberPwCheck !== `` &&
              this.$data.memberPwCheck !== undefined)
          ) {
            if (value === undefined || value === ``) {
              return false;
            } else {
              return true;
            }
          } else {
            return true;
          }
        },
        maxLength: maxLength(20),
      },
      memberName: {
        required,
        maxLength: maxLength(20),
      },
      loginFailCnt: {
        required,
      },
      expired: {
        required,
      },
    },
    memberPwCheck: {
      required(value: string) {
        if (
          !this.$data.isUpdate ||
          (this.$data.member.memberPw !== `` &&
            this.$data.member.memberPw !== undefined)
        ) {
          if (value === undefined || value === ``) {
            return false;
          } else {
            return true;
          }
        } else {
          return true;
        }
      },
      maxLength: maxLength(20),
    },
  },
})
export default class ManageMember extends Vue {
  readonly $moment: any;
  readonly delayTouch: typeof delayTouch = delayTouch;
  readonly getVErrors: typeof getVErrors = getVErrors;
  MEMBER_TYPE?: SelectItem[] = [];
  dialog: boolean = false;
  isUpdate: boolean = true;
  headers: DataTableHeader[] = [];
  members: Member[] = [];
  member: Member = {
    expired: this.$moment()
      .add(1, 'months')
      .endOf(`day`)
      .toDate(),
  };
  memberPwCheck: string = ``;

  created() {
    this.headers = [
      {
        text: ` `,
        align: `start`,
        value: `memberId`,
      },
      { text: ` `, value: `memberName` },
      { text: ` `, value: `memberType` },
      { text: `  `, value: `loginFailCnt` },
      { text: `  `, value: `closeTf` },
      { text: `  `, value: `expired` },
      { text: `Actions`, value: `action`, sortable: false },
    ];
    this.getList();
    this.getMemberType();
  }

  get formTitle() {
    return this.isUpdate ? ` ` : ` `;
  }

  async getList() {
    const response = await getOnlyListDataApi<Member[]>(`sample/admin/member/`);
    this.members = response.data || [];
  }

  async getMemberType() {
    this.MEMBER_TYPE = await getCodeListDataApi(`MEMBER_TYPE`);
  }

  editItem(member: Member) {
    (this.$v.member as Validation).$reset();
    this.isUpdate = member !== undefined;
    if (member === undefined) {
      this.member = Object.assign(
        {},
        {
          closeTf: false,
          expired: this.$moment()
            .add(1, 'months')
            .endOf(`day`)
            .toDate(),
        },
      );
      this.memberPwCheck = ``;
    } else {
      this.member = Object.assign({}, member);
    }
    this.dialog = true;
  }

  async deleteItem(member: Member) {
    if (!confirm(`Are you sure you want to delete this member?`)) {
      return;
    }

    const response = await deleteDataApi<Member>(
      `sample/admin/member/`,
      member.memberId!,
      this,
    );
    if (_.startsWith(response.code, `S`)) {
      this.getList();
      this.closeModal();
    }
  }

  closeModal() {
    this.dialog = false;
  }

  async save() {
    const $vForm: Validation = this.$v.member as Validation;
    $vForm.$touch();
    const valid = !$vForm.$pending && !$vForm.$error;
    if (!valid) {
      this.$toast.warning('    .');
      return;
    }
    this.isUpdate ? this.patch() : this.create();
  }

  async create() {
    const response = await createDataApi<Member>(
      `sample/admin/member/`,
      this.member,
      this,
    );
    if (_.startsWith(response.code, `S`)) {
      this.getList();
      this.closeModal();
    }
  }

  async patch() {
    const response = await patchDataApi<Member>(
      `sample/admin/member/`,
      this.member,
      this.member.memberId!,
      this,
    );
    if (_.startsWith(response.code, `S`)) {
      this.getList();
      this.closeModal();
    }
  }
}
